.app-container {
  text-align: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f5f5f5;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  width: 300px;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.app-title {
  font-size: 24px;
  margin-bottom: 10px;
}

.counter-value {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
}

.counter-button {
  padding: 10px 20px;
  font-size: 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px;
  transition: background-color 0.3s;
}

.increase-button {
  background-color: red;
  color: #fff;
}

.decrease-button {
  background-color: green;
  color: #fff;
}

.counter-button:hover {
  background-color: #0056b3;
}

.counter-button span {
  display: inline-block;
  padding: 5px 10px;
}

.counter-wrapper {
  display: flex;
  justify-content: center;
}
body {
  background-color: rgb(32, 218, 178);
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}